#github {
  position: relative;
  width: 20%;
  height: 100%;
  fill: url(#githubGradient) #fff;
}
#githubGradient {
  --topFade: #f093fb;
  --bottomFade: #f5576c;
}

#linkedIn {
  position: relative;
  width: 20%;
  height: 100%;
  fill: url(#githubGradient) #fff;
}
#linkedInGradient {
  --topFade: #f093fb;
  --bottomFade: #f5576c;
}
#linkedIn,
#github {
  opacity: 0;
  transition: all .3s;
  cursor: grab;
}

#projects {
